import React, {useState} from 'react';

const CounterItem = ({counters, counter, deleted, sumAll}) => {
    const [count, setCount] = useState(0)
    const [str, setStr] = useState('Введено четное число')
    const [bgColor, setBgColor] = useState('#a47053')


    if(counter.value > count) {
        check(counter.value)
        setCount(count+1)
    }

    if(counter.value < count) {
        check(counter.value)
        setCount(count - 1)
    }

    function increment() {
        setCount(count + 1)
        // check(count + 1)
        counter.value = count + 1
        check((counter.value))
    }

    function decrement() {
        setCount(count - 1)
        counter.value = count - 1
        check((counter.value))
    }

    function reset() {
        setCount(0)
        counter.value = 0
        check(counter.value)
    }

    function check(count) {
        if (count % 2 === 0) {
            setStr('Введено четное число')
            setBgColor ('#a47053')
        } else {
            setBgColor ('#efca66')
            setStr('Введено нечетное число')
        }
        if (count < 0) {
            setBgColor ('#a47053')
            counter.value = 0
            setCount(counter.value)
            setStr('Введено четное число')
        }
    }

    return (
        <div className="container" style={{backgroundColor: bgColor}}>
            <h1 className="title__count" onChange={sumAll(counters)}>{counter.value}</h1>
            <h1 className="title__str">{str}</h1>
            <div className="btn__block">
                {/*<h1>{props.counter.id}</h1>*/}
                <button onClick={increment}>+</button>
                <button onClick={reset}>Reset</button>
                <button onClick={decrement}>-</button>
                <button onClick={() => deleted(counter)}>Delete</button>
            </div>
        </div>
    );
};

export default CounterItem;